body {
  margin: 0;
  background: #000;
  padding: 0; }

#visualizer {
  position: absolute;
  right: 5px;
  bottom: 5px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 250px; }

.video {
  position: absolute;
  top: 0;
  left: 0; }

.sequence {
  position: absolute;
  top: 0;
  left: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%; }



